						formatter: function(name) {
							// legend
							let data = devicePercentArray;
							let total = 0;
							let tarValue = 0;
							for (let i = 0, l = data.length; i < l; i++) {
								total += data[i].value;
								if (data[i].name == name) {
									tarValue = data[i].value;
								}
							}
							let p = (tarValue / total * 100).toFixed(0);
							let arr = [
								'{a|' + name + '}{b|' + p + '%}'
							];

							return arr.join('\n')
						},
						left: 280,
						top: 50,
						icon: 'circle',
