			drawDevicePercentChart() {
				let deviceArray = new Array();
				let min = 20;
				let max = 200;
				let numDeviceSum = 0;
				for (let i = 0; i < 6; i++) {
					deviceArray[i] = Math.floor(min + Math.random() * (max - min));
					numDeviceSum += deviceArray[i];
				}
				let devicePercentArray = [{
					value: deviceArray[0],
					name: ''
				},
				{
					value: deviceArray[1],
					name: '()'
				},
				{
					value: deviceArray[2],
					name: ''
				},
				{
					value: deviceArray[3],
					name: '()'
				},
				{
					value: deviceArray[4],
					name: 'i-tech()'
				},
				{
					value: deviceArray[5],
					name: '()'
				}];
				this.devicePercentChart.setOption({
					tooltip: {
						trigger: 'item',
						backgroundColor: 'rgba(255, 255, 255, 0.85)',
						formatter: function(param) {
							return param.marker + " " + param.name + " " + param.value;
						},
						extraCssText: ' height:30px; box-shadow: 0 3 6px rgba(0, 0, 0, 0.12);',
						textStyle: {
							color: '#262626',
							fontSize: 12,
							fontFamily: 'PingFang SC',
							fontWeight: 400,
							lineHeight: 18,
						},
					},
					color: ["#47A1FF", "#9860E5", "#F3657C", "#FAD34B", "#7BD58D", "#4ECBCC"],
					legend: {
						formatter: function(name) {
							// legend
							let data = devicePercentArray;
							let total = 0;
							let tarValue = 0;
							for (let i = 0, l = data.length; i < l; i++) {
								total += data[i].value;
								if (data[i].name == name) {
									tarValue = data[i].value;
								}
							}
							let p = (tarValue / total * 100).toFixed(0);
							let arr = [
								'{a|' + name + '}{b|' + p + '%}'
							];

							return arr.join('\n')
						},
						left: 280,
						top: 50,
						icon: 'circle',
						itemWidth: 8,
						itemHeight: 8,
						orient: 'vertical',
						data: ['', '()', '', '()', 'i-tech()', '()'],
