									fontWeight: 'bold',
									color: "#000000",
									lineHeight: 18,
									opacity: 0.85,
									align: 'left',
									width: 50,
								},
							},
						},
					},
					series: [{
						name: '',
						type: 'pie',
						radius: ['45%', '60%'],
						center: [130, 135],
						label: {
							show: false,
						},
